/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();
	
	&.mkdf-info-on-image {
		
		.mkdf-pli {
			
			.mkdf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .mkdf-pli {
        padding: 0;
        z-index: 100;
		text-align: center;

	    .mkdf-pli-title {
		    margin: 13px 0 4px;
			font-weight: 400;
	    }
	
	    .mkdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .mkdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .mkdf-pli-rating-holder {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .mkdf-pli-rating {
		    @include mkdfWooCommerceRatingsStyle();
	    }

        .mkdf-pli-price {
	        @include mkdfWooCommercePriceStyle();
			position: absolute;
			bottom: 73px;
			left: -11px;
			margin: 0;
			background-color: #fff;
			padding: 10px 9px 11px 12px;
			opacity: 0;
			@include mkdfTransition(all .2s ease);
			z-index: 150;
        }
    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        &:hover {

			.mkdf-pli-image img {
				margin: -13px 0 13px !important;
				@include mkdfTransition(all .2s ease);
			}

            .mkdf-pli-price {
                opacity: 1;
				@include mkdfTransition(all .2s ease);
            }
        }

        .touchevents & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
				@include mkdfTransition(all .2s ease);
				//-webkit-box-shadow: 5px 9px 20.16px .84px rgba(0,0,0,.42);
				//-moz-box-shadow:  5px 9px 20.16px .84px rgba(0,0,0,.42);
				//box-shadow:  5px 9px 20.16px .84px rgba(0,0,0,.42);
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
	            @include mkdfWooCommerceMarksGeneralStyle();
				left: -18px;
            }
        }
            
        .mkdf-pli-text {
            @include mkdfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            background-color: rgba($default-heading-color, 0.6);
            box-sizing: border-box;
            @include mkdfTransition(opacity .2s ease-out);
        }

        .mkdf-pli-text-outer {
	        @include mkdfTableLayout();
        }

        .mkdf-pli-text-inner {
            @include mkdfTableCellLayout();

            .mkdf-pli-title,
            .mkdf-pli-category a,
            .mkdf-pli-excerpt,
            .mkdf-pli-rating,
            .mkdf-pli-price {
                color: #fff;
            }
	
	        .mkdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }
        }

	    .mkdf-pli-link {
		    @include mkdfAbsoluteHolderLayout();
	    }
    }

    .mkdf-pli-text-wrapper {
        @include mkdfRelativeHolderLayout();
        margin: 20px 0 0;
		text-align: center;

		.mkdf-pli-add-to-cart {

			&.mkdf-light-skin {

				.button,
				.added_to_cart {
					color: #fff;

					&:hover {
						color: $gray-color;
					}
				}
			}

			&.mkdf-dark-skin {

				.button,
				.added_to_cart {
					color: $default-heading-color;

					&:hover {
						color: $gray-color;
					}
				}
			}

			.button {
				&:hover {
					color: $gray-color;
				}
			}
		}

		.button.added {
			display: none;
		}
    }
	
	&.mkdf-product-info-light {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: #fff;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.mkdf-product-info-dark {
		
		.mkdf-pli-inner {
			
			.mkdf-pli-text-inner {
				
				.mkdf-pli-title,
				.mkdf-pli-category a,
				.mkdf-pli-excerpt,
				.mkdf-pli-rating,
				.mkdf-pli-price {
					color: $default-heading-color;
				}
				
				.mkdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.mkdf-masonry-layout {
		
		.mkdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.mkdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */