/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.mkdf-content .woocommerce {
    @include mkdfRelativeHolderLayout();
}

.mkdf-pl-main-holder {
    margin: 0;
    
    .mkdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        text-align: center;
        @include mkdfTransition(all .2s ease);

        .mkdf-pl-image img {
            backface-visibility: hidden;
        }

        &:hover {
            .mkdf-pl-image img{
                @include mkdfTransform(translateY(-10px));
                @include mkdfTransition(all .2s ease);
            }

            .price {
                opacity: 1;
                @include mkdfTransition(all .2s ease);
            }
        }

        .mkdf-woocommerce-columns-3 & {
            border-right: 1px solid #f2f2f2;

            &:nth-of-type(3n) {
                border-right: none;
            }
        }
        
        .touchevents & {
            cursor: pointer;
        }
        
        .mkdf-product-list-title {
            margin: 12px 0 2px;
        }
        
        .mkdf-pl-rating-holder {
            @include mkdfRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }
        
        .price {
            @include mkdfWooCommercePriceStyle();
            position: absolute;
            bottom: 75px;
            left: 20px;
            background-color: #fff;
            box-sizing: border-box;
            padding: 10px 9px 11px 12px;
            -webkit-box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.09);
            -moz-box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.09);
            box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.09);
            opacity: 0;
            @include mkdfTransition(all .2s ease);
            font-size: 15px;

            &:before {
                content: 'Buy for ';
                position: relative;
            }
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
    
            .mkdf-woo-pl-info-below-image & {
                margin: 0;
                border: none;
                background-color:  transparent;
                color: $default-heading-color;
                font-style: italic;
                text-transform: none;
                font-family: $default-text-font;
                padding: 0;
                font-size: 15px;
                letter-spacing: 0;

                &:hover {
                    background-color:  transparent;
                }
            }
        }
        
        .button.added {
            display: none;
        }
        
        .mkdf-pl-inner {
            position: relative;
            padding-top: 33px;
            
            .mkdf-pl-image {
                position: relative;
                display: inline-block;
                
                img {
                    display: block;
                    margin: 0 auto;
                    @include mkdfTransition(all .3s ease);
                }
            }
            
            .mkdf-pl-text-outer {
                position: relative;
                text-align: center;
                display: inline-block;
            }
            
            .mkdf-pl-text-inner {
                display: table;

                .mkdf-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include mkdfAbsoluteHolderLayout();
            }
        }
        
        .mkdf-pl-text-wrapper {
            @include mkdfRelativeHolderLayout();
            margin: 0 0 38px;
            text-align: center;
        }
    }
}

$columns_space_label: ('no', 'micro', 'tiny', 'small', 'normal', 'medium', 'large', 'huge');
$columns_space_width: (0, 2, 5, 10, 15, 20, 25, 40);

@for $i from 0 to length($columns_space_label) {
    &.mkdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.mkdf-woo-huge-space ul.products>.product {
    margin-bottom: 40px;
}

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */