/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0;

    .touchevents & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;
        color: $gray-color;

        .mkdf-header-bottom & {
            color: #fff;
        }

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
        }

        &.mkdf-header-cart-icon-pack {
            
            .mkdf-cart-icon {
                font-size: 18px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }

            .mkdf-cart-number {
                font-size: 9px;
                font-family: $default-heading-font;
                border-radius: 100%;
                background-color: #f7775e;
                color: #fff;
                height: 12px;
                width: 12px;
                line-height: 12px;
                text-align: center;
                position: relative;
                left: -10px;
                top: -11px;
                display: inline-block;
            }
        }

        &.mkdf-header-cart-svg-path {
            
            .mkdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .mkdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart{
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-dark-header .mkdf-top-bar .mkdf-shopping-cart-holder .mkdf-header-cart{
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart,
.mkdf-light-header .mkdf-top-bar .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 240px;
    background-color: #fff;
    border: 1px solid #f3f3f3;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include mkdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 20px 17px 15px 17px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 10px 0 3px;
            border-bottom: 1px solid $default-border-color;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                color: initial;
                border-bottom: 0;
                font-size: 10px;
                text-transform: uppercase;
                font-weight: 500;
                font-family: $default-heading-font;
                letter-spacing: .2em;
                padding-bottom: 5px !important;
                text-align: center;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
                border-bottom: none;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
            //-webkit-box-shadow: 5px 9px 20.16px .84px rgba(0,0,0,.42);
            //-moz-box-shadow: 5px 9px 20.16px .84px rgba(0,0,0,.42);
            //box-shadow: 5px 9px 20.16px .84px rgba(0,0,0,.42);
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        vertical-align: top;
        padding-left: 1px;

        .mkdf-product-title {
            display: block;
            margin: 0;
            padding: 0 15px 5px 0;
            box-sizing: border-box;
            color: $default-heading-color;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 14px;
	        line-height: 1;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity,
        .amount {
            display: inline-block;
            font-style: italic;
            font-size: 12px;
            margin: 2px 0 0;
            color: $default-heading-color;
        }
    }

    .mkdf-cart-bottom {
        padding: 10px 0 0;

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 11px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }

            .mkdf-total {
                font-family: $default-heading-font;
                font-size: 12px;
                letter-spacing: .2em;
            }

            .mkdf-total-amount {
                text-align: right;
	            font-size: 14px;
                font-style: italic;
            }
        }

        .mkdf-my-cart,
        .mkdf-checkout {
            @include mkdfButtonDefaultStyle();
            @include mkdfButtonSolidColor();
            padding: 9px 14px 8px;
            font-size: 8px;

            span {
                display: block;
            }
        }

        .mkdf-my-cart {
            background-color: $first-main-color;
        }

        .mkdf-checkout {
            float: right;
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */