/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    margin: 0;
    color: $default-heading-color;
    font-size: 44px;
    line-height: 1em;
    letter-spacing: 0;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include mkdfTransform(translateZ(0));
    font-family: $additional-font;
}

@mixin mkdfWooCommerceSaleStyle() {
    left: 0;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    left: 0;
}

@mixin mkdfWooCommerceNewProductStyle() {
    left: 0;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1em;
    color: $default-heading-color;
    font-style: italic;
    
    * {
        font-size: 15px;
        line-height: inherit;
    }
    
    del {
        margin: 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
        color: #f7775e;

        .amount {
            margin-left: 5px;
        }

        &:before {
            content: '-';
        }
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1em;
    letter-spacing: 5px;
    color: #e8d931;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: -4px;
        bottom: 8px;
        display: block;
        z-index: -1;
        letter-spacing: 9px;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */