/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {
    .screen-reader-text {
        height: auto;
        width: auto;
        clip: unset;
        line-height: 43px;
        position: relative !important;
        font-size: 12px;
        font-style: italic;
        color: $gray-color;
        display: block;
        text-align: left;
        top: 0;
        left: 16px;
        float: left;
        clip-path: none;
    }
    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        border: 1px solid $default-border-color;
        width: 192px;
        box-sizing: border-box;
        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            text-align: center;
            box-sizing: border-box;
        }
        .mkdf-quantity-input {
            position: relative;
            width: auto;
            padding: 0;
            margin: 0;
            line-height: 43px;
            outline: 0;
            border: none;
            left: 51px;
            font-family: $default-heading-font;
            font-size: 14px;
        }
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: relative;
            right: 0;
            width: auto;
            height: auto;
            line-height: 43px;
            cursor: pointer;
            @include mkdfTransition(color .15s ease-in-out);
            font-family: "dripicons-v2";
            z-index: 2;
            &:before {
                display: none;
            }
            &:hover {
                color: $gray-color;
            }
        }
        .mkdf-quantity-plus {
            left: 33px;
            &:after {
                content: "\56";
            }
        }
        .mkdf-quantity-minus {
            left: 68px;
            &:after {
                content: "\55";
            }
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {
    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }
    .coupon {
        input[type="text"] {
            width: 200px;
            height: 45px;
        }
    }
}


/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */